import * as React from 'react';
import VideoListItem from './video_list_item';

const VideosList = (propsObj) => {

    //destruct propsObj param
    const { data_videos } = propsObj;

    //return HTML
    return (
        <ul className="col-md-4 list-group">

            {data_videos.map(function (videoDataItem, i) {
                return <VideoListItem key={videoDataItem.etag} data_id={i} data_video={videoDataItem} />
            })}

        </ul>
    );
}

export default VideosList


